import { _decorator, Component, Node, Label, tween, Vec3 } from 'cc';
const { ccclass, property } = _decorator;

@ccclass('tips')
export class tips extends Component {

    @property(Label)
    tips_lb: Label;

    start() {

    }

    update(deltaTime: number) {

    }

    onShow(text: string) {
        this.tips_lb.string = text;
        tween(this.node)
            .to(1.0, { position: new Vec3(0, 10, 0) })
            // to 动作完成后会调用该方法     
            .removeSelf()
            .start()
    }
}

